@import "./_job.less";
@job-img-dir: 'job/_images';

.modify{
	width:100%;
	background:#fff;
	line-height:90*@rex;
	overflow:hidden;
	position:relative;

	.content{
		width:215*@rex;
		margin:0 auto;
		overflow:hidden;

		i{
			display:block;
			.size(25*@rex,24*@rex);
			float:left;
			margin-right:10*@rex;
			background: data-uri('@{job-img-dir}/lv.png');
			background-size: 100% 100%;
			margin-top:33*@rex;
		}

		span{
			display:block;
			color:#0F0F0F;
			float:left;
			font-size:16*@rex;
		}
	}
}

a.index{
	display:block;
	width:94%;
	.margin(3%);
	line-height:40*@rex;
	text-align:center;
	color:#fff;
	background:#C20053;
	border-radius:4*@rex;
	border:none;
}

a.job-index{
	display:block;
	width:94%;
	.margin(3%);
	line-height:40*@rex;
	text-align:center;
	color:#C20053;
	background:#fff;
	border-radius:4*@rex;
	border:none;
}